<template>
  <div id="group">
    <!-- begin breadcrumb -->
    <ol class="breadcrumb pull-right">
      <li><a href="javascript:;">Home</a></li>
      <li class="active">{{ page.title }}</li>
    </ol>
    <!-- end breadcrumb -->
    <!-- begin page-header -->
    <h1 class="page-header">{{ page.title }} <small>{{ page.subtitle }}</small></h1>
    <!-- end page-header -->

    <!-- Info Box -->
    <div class="row">
      <!-- begin col-3 -->
      <div class="col-6">
        <div class="widget widget-stats bg-purple">
          <div class="stats-icon"><i class="fa fa-user"></i></div>
          <div class="stats-info">
            <h4>Anggota Aktif</h4>
            <p>1.291.922</p>
          </div>
          <div class="stats-link">
          </div>
        </div>
      </div>
      <!-- end col-3 -->
      <!-- begin col-3 -->
      <div class="col-6">
        <div class="widget widget-stats bg-red">
          <div class="stats-icon"><i class="fa fa-user"></i></div>
          <div class="stats-info">
            <h4>Anggota Tidak Aktif</h4>
            <p>51.631</p>
          </div>
          <div class="stats-link">
          </div>
        </div>
      </div>
      <!-- end col-3 -->
    </div>

    <div class="row">
      <div class="col-6">
        <!-- begin profile-container -->
          <div class="profile-container">
              <!-- begin profile-section -->
              <div class="profile-section">
                  <div class="profile-info">
                      <!-- begin table -->
                      <div class="table-responsive">
                          <table class="table table-profile">
                              <thead>
                                  <tr>
                                      <th align="right">
                                        <img :src="group.image" class="group-icon">
                                      </th>
                                      <th>
                                          <h4>{{ group.name }} <small>{{ group.code }}</small></h4>
                                      </th>
                                  </tr>
                              </thead>
                              <tbody>
                                  <tr class="highlight">
                                      <td class="field">Deskripsi</td>
                                      <td>{{ group.description }}</td>
                                  </tr>
                                  <tr class="divider">
                                      <td colspan="2"></td>
                                  </tr>
                                  <tr>
                                      <td class="field">Privasi</td>
                                      <td>Publik</td>
                                  </tr>
                                  <tr>
                                      <td class="field">Jumlah Anggota</td>
                                      <td>{{ group.members }}</td>
                                  </tr>
                                  <tr>
                                      <td class="field">Link Grup</td>
                                      <td><code class="">{{ group.dynamic_link }}</code></td>
                                  </tr>
                              </tbody>
                          </table>
                      </div>
                      <!-- end table -->
                  </div>
                  <!-- end profile-info -->
              </div>
              <!-- end profile-section -->
          </div>
        <!-- end profile-container -->
      </div>

      <div class="col-6">
        <memberActiveChart></memberActiveChart>
      </div>
    </div>

    <br>
    <div class="row">
      <div class="col-md-12">
        <div class="panel panel-inverse">
            <div class="panel-heading">
                <div class="panel-heading-btn">
                    <a href="javascript:;" class="btn btn-xs btn-icon btn-circle btn-default" data-click="panel-expand"><i class="fa fa-expand"></i></a>
                    <a href="javascript:;" class="btn btn-xs btn-icon btn-circle btn-success" data-click="panel-reload"><i class="fa fa-repeat"></i></a>
                </div>
                <h4 class="panel-title">Daftar Anggota</h4>
            </div>
            <div class="panel-body">
              <div class="filterTable">
                <div class="row">
                  <div class="col-4">
                    Aktifitas :
                    <select class="select2" multiple="multiple">
                      <option value="">Semua Aktivitas</option>
                      <option value="">Shalat Tahajud</option>
                      <option value="">Shalat Shubuh Berjamaah</option>
                      <option value="">Tilawah Min 1 Juz</option>
                      <option value="">Infaq</option>
                      <option value="">Menuntut Ilmu</option>
                      <option value="">Membantu Orang Lain</option>
                      <option value="">Silaturrahim</option>
                    </select>
                  </div>
                  <div class="col-4">
                    Anggota Aktif :
                    <select class="select2">
                      <option value="">Semua Periode</option>
                      <option value="">7 hari terakhir</option>
                      <option value="">30 hari terakhir</option>
                    </select>
                  </div>
                  <div class="col-4">
                    Anggota Konsisten :
                    <select class="select2">
                      <option value="">Semua Periode</option>
                      <option value="">7 hari terakhir</option>
                      <option value="">30 hari terakhir</option>
                    </select>
                  </div>
                </div>
              </div>
              <br>
              <table id="data-table" class="table table-striped table-bordered">
                  <thead>
                      <tr>
                          <th>No</th>
                          <th>Nama Anggota</th>
                          <th>Terakhir Aktif</th>
                          <th>Aksi</th>
                      </tr>
                  </thead>
                  <tbody>

                  </tbody>
              </table>
            </div>
        </div>
    </div>
    </div>

  </div>
</template>
<script>
import MemberActiveChart from '@/components/MemberActiveChart';
export default {
  name: 'group',
  components: {
    'memberActiveChart': MemberActiveChart,
  },
  data () {
    return {
      page: {
        title: 'Manajemen Grup - ' + this.$route.params.code,
        subtitle: 'Detail informasi mengenai grup yang Anda kelola.'
      },
      group: {
        code: this.$route.params.code,
        name: 'Ahijra Found',
        description: 'Mari bersama sama menyelesaikan masalah dgn cara Allah, mewujudkan Hajat dgn cara Allah, demi mencapai ridho Nya. Karna sebaik2nya cara adalah Cara -Nya. Semoga Allah meridhoi',
        is_public: 1,
        members: 2314,
        dynamic_link: 'https://g8bvp.app.goo.gl/XZry7K1effIWQBQw1',
        image: '../src/assets/img/group/img_quran.png'
      }
    }
  },
  mounted () {
    this.handleGrouplist();
    $('.select2').select2();
  },
  methods: {
    handleGrouplist() {
      if ($('#data-table').length !== 0) {
        $('#data-table').DataTable({
            // ajax: 'src/assets/data/group.json',
            dom: 'lBfrtip',
            buttons: [
                { extend: 'copy', className: 'btn-sm' },
                { extend: 'csv', className: 'btn-sm' },
                { extend: 'excel', className: 'btn-sm' },
                { extend: 'pdf', className: 'btn-sm' },
                { extend: 'print', className: 'btn-sm' }
            ],
            responsive: true,
            // keys: true,
            // select: true
        });
      }
    }
  }
}
</script>
<style scoped>
  img.group-icon {
    max-width: 96px;
  }
  .select2 {
    width: 200px;
  }
</style>



