<template>
  <!-- begin #header -->
  <div id="app-header" class="header navbar navbar-default navbar-fixed-top">
    <!-- begin container-fluid -->
    <div class="container-fluid">
      <!-- begin mobile sidebar expand / collapse button -->
      <div class="navbar-header">
        <a href="javascript:;" class="navbar-brand">
          Yawme Insight
        </a>
        <button type="button" class="navbar-toggle" data-click="sidebar-toggled">
          <span class="icon-bar"></span>
          <span class="icon-bar"></span>
          <span class="icon-bar"></span>
        </button>
      </div>
      <!-- end mobile sidebar expand / collapse button -->

      <!-- begin header navigation right -->
      <ul class="nav navbar-nav navbar-right">
        <li class="dropdown navbar-user">
          <a href="javascript:;" class="dropdown-toggle" data-toggle="dropdown">
            <img src="~@/assets/img/user-11.jpg" alt="" />
            <span class="hidden-xs">Abdul</span> <b class="caret"></b>
          </a>
          <ul class="dropdown-menu animated fadeInLeft">
            <li class="arrow"></li>
            <li class="divider"></li>
            <li><a href="javascript:;" v-on:click="doLogout">Log Out</a></li>
          </ul>
        </li>
      </ul>
      <!-- end header navigation right -->
    </div>
    <!-- end container-fluid -->
  </div>
  <!-- end #header -->
</template>
<script>
import auth from '@/services/auth.js'

export default {
  name: 'header',
  methods: {
    doLogout () {
      auth.logout();
    }
  }
}
</script>
<style lang="scss" scoped>
  @import url('./layout.scss');
  @import url('../../app.scss');
</style>



