<template>
  <div id="home">
    <!-- begin breadcrumb -->
    <ol class="breadcrumb pull-right">
      <li><a href="javascript:;">Home</a></li>
      <li class="active">{{ page.title }}</li>
    </ol>
    <!-- end breadcrumb -->
    <!-- begin page-header -->
    <h1 class="page-header">{{ page.title }} <small>{{ page.subtitle }}</small></h1>
    <!-- end page-header -->

    <!-- Info Box -->
    <div class="row">
      <!-- begin col-3 -->
      <div class="col-md-3 col-sm-6">
        <div class="widget widget-stats bg-green">
          <div class="stats-icon"><i class="fa fa-users"></i></div>
          <div class="stats-info">
            <h4>Grup Aktif</h4>
            <p>52.548</p>
          </div>
          <div class="stats-link">
          </div>
        </div>
      </div>
      <!-- end col-3 -->
      <!-- begin col-3 -->
      <div class="col-md-3 col-sm-6">
        <div class="widget widget-stats bg-blue">
          <div class="stats-icon"><i class="fa fa-users"></i></div>
          <div class="stats-info">
            <h4>Grup Tidak Aktif</h4>
            <p>12.408</p>
          </div>
          <div class="stats-link">
          </div>
        </div>
      </div>
      <!-- end col-3 -->
      <!-- begin col-3 -->
      <div class="col-md-3 col-sm-6">
        <div class="widget widget-stats bg-purple">
          <div class="stats-icon"><i class="fa fa-user"></i></div>
          <div class="stats-info">
            <h4>Anggota Aktif</h4>
            <p>1.291.922</p>
          </div>
          <div class="stats-link">
          </div>
        </div>
      </div>
      <!-- end col-3 -->
      <!-- begin col-3 -->
      <div class="col-md-3 col-sm-6">
        <div class="widget widget-stats bg-red">
          <div class="stats-icon"><i class="fa fa-user"></i></div>
          <div class="stats-info">
            <h4>Anggota Tidak Aktif</h4>
            <p>51.631</p>
          </div>
          <div class="stats-link">
          </div>
        </div>
      </div>
      <!-- end col-3 -->
    </div>

    <!-- Info Chart -->
    <div class="row">
      <!-- begin col-8 -->
      <div class="col-md-6">
        <div class="panel panel-inverse" data-sortable-id="index-1">
          <div class="panel-heading">
            <div class="panel-heading-btn">
              <a href="javascript:;" class="btn btn-xs btn-icon btn-circle btn-default" data-click="panel-expand"><i class="fa fa-expand"></i></a>
              <a href="javascript:;" class="btn btn-xs btn-icon btn-circle btn-success" data-click="panel-reload"><i class="fa fa-repeat"></i></a>
            </div>
            <h4 class="panel-title">Statistik Grup (Last 7 Days)</h4>
          </div>
          <div class="panel-body">
            <canvas id="group-chart" class="height-sm"></canvas>
          </div>
        </div>
      </div>
      <!-- end col-8 -->
      <!-- begin col-8 -->
      <div class="col-md-6">
        <div class="panel panel-inverse" data-sortable-id="index-1">
          <div class="panel-heading">
            <div class="panel-heading-btn">
              <a href="javascript:;" class="btn btn-xs btn-icon btn-circle btn-default" data-click="panel-expand"><i class="fa fa-expand"></i></a>
              <a href="javascript:;" class="btn btn-xs btn-icon btn-circle btn-success" data-click="panel-reload"><i class="fa fa-repeat"></i></a>
            </div>
            <h4 class="panel-title">Statistik Anggota (Last 7 Days)</h4>
          </div>
          <div class="panel-body">
            <canvas id="member-chart" class="height-sm"></canvas>
          </div>
        </div>
      </div>
      <!-- end col-8 -->
    </div>
  </div>
</template>
<script>

import Chart from 'chart.js'

export default {
  name: 'home',
  data() {
    return {
      page: {
        title: 'Dashboard',
        subtitle: 'informasi menyeluruh terkait grup dan anggota Anda.',
      }
    }
  },
  mounted() {
    this.handleGroupChart();
    this.handleMemberChart();
  },
  methods: {
    randomScalingFactor () {
      return Math.round(Math.random()*100)
    },
    handleGroupChart () {
      var lineChartData = {
          labels: ['January', 'February', 'March', 'April', 'May', 'June', 'July'],
          datasets: [{
              label: 'Grup Aktif',
              borderColor: '#22ff98',
              pointBackgroundColor: '#22ff98',
              pointRadius: 2,
              borderWidth: 2,
              fill: false,
              data: [this.randomScalingFactor(), this.randomScalingFactor(), this.randomScalingFactor(), this.randomScalingFactor(), this.randomScalingFactor(), this.randomScalingFactor(), this.randomScalingFactor()]
          }, {
              label: 'Grup Tidak Aktif',
              borderColor: '#ef2d47',
              pointBackgroundColor: '#ef2d47',
              pointRadius: 2,
              borderWidth: 2,
              fill: false,
              data: [this.randomScalingFactor(), this.randomScalingFactor(), this.randomScalingFactor(), this.randomScalingFactor(), this.randomScalingFactor(), this.randomScalingFactor(), this.randomScalingFactor()]
          }]
      };
      var ctx = $('#group-chart');
      var myChart = new Chart(ctx, {
          type: 'bar',
          data: lineChartData,
      });
    },
    handleMemberChart () {
      var lineChartData = {
          labels: ['January', 'February', 'March', 'April', 'May', 'June', 'July'],
          datasets: [{
              label: 'Anggota Aktif',
              borderColor: '#22ff98',
              pointBackgroundColor: '#22ff98',
              pointRadius: 2,
              borderWidth: 2,
              fill: false,
              data: [this.randomScalingFactor(), this.randomScalingFactor(), this.randomScalingFactor(), this.randomScalingFactor(), this.randomScalingFactor(), this.randomScalingFactor(), this.randomScalingFactor()]
          }, {
              label: 'Anggota Tidak Aktif',
              borderColor: '#ef2d47',
              pointBackgroundColor: '#ef2d47',
              pointRadius: 2,
              borderWidth: 2,
              fill: false,
              data: [this.randomScalingFactor(), this.randomScalingFactor(), this.randomScalingFactor(), this.randomScalingFactor(), this.randomScalingFactor(), this.randomScalingFactor(), this.randomScalingFactor()]
          }]
      };
      var ctx = $('#member-chart');
      var myChart = new Chart(ctx, {
          type: 'bar',
          data: lineChartData,
      });
    }
  }
}
</script>
<style lang="scss" scoped>
  @import url('./home.scss');
</style>



