<template>
  <div id="grouplist">
    <!-- begin breadcrumb -->
    <ol class="breadcrumb pull-right">
      <li><a href="javascript:;">Home</a></li>
      <li class="active">{{ page.title }}</li>
    </ol>
    <!-- end breadcrumb -->
    <!-- begin page-header -->
    <h1 class="page-header">{{ page.title }} <small>{{ page.subtitle }}</small></h1>
    <!-- end page-header -->

    <!-- Info Box -->
    <div class="row">
      <!-- begin col-3 -->
      <div class="col-md-3 col-sm-6">
        <div class="widget widget-stats bg-green">
          <div class="stats-icon"><i class="fa fa-users"></i></div>
          <div class="stats-info">
            <h4>Grup Aktif</h4>
            <p>52.548</p>
          </div>
          <div class="stats-link">
          </div>
        </div>
      </div>
      <!-- end col-3 -->
      <!-- begin col-3 -->
      <div class="col-md-3 col-sm-6">
        <div class="widget widget-stats bg-blue">
          <div class="stats-icon"><i class="fa fa-users"></i></div>
          <div class="stats-info">
            <h4>Grup Tidak Aktif</h4>
            <p>12.408</p>
          </div>
          <div class="stats-link">
          </div>
        </div>
      </div>
      <!-- end col-3 -->
      <!-- begin col-3 -->
      <div class="col-md-3 col-sm-6">
        <div class="widget widget-stats bg-purple">
          <div class="stats-icon"><i class="fa fa-user"></i></div>
          <div class="stats-info">
            <h4>Anggota Aktif</h4>
            <p>1.291.922</p>
          </div>
          <div class="stats-link">
          </div>
        </div>
      </div>
      <!-- end col-3 -->
      <!-- begin col-3 -->
      <div class="col-md-3 col-sm-6">
        <div class="widget widget-stats bg-red">
          <div class="stats-icon"><i class="fa fa-user"></i></div>
          <div class="stats-info">
            <h4>Anggota Tidak Aktif</h4>
            <p>51.631</p>
          </div>
          <div class="stats-link">
          </div>
        </div>
      </div>
      <!-- end col-3 -->
    </div>

    <div class="row">
      <div class="col-md-12">
        <div class="panel panel-inverse">
            <div class="panel-heading">
                <div class="panel-heading-btn">
                    <a href="javascript:;" class="btn btn-xs btn-icon btn-circle btn-default" data-click="panel-expand"><i class="fa fa-expand"></i></a>
                    <a href="javascript:;" class="btn btn-xs btn-icon btn-circle btn-success" data-click="panel-reload"><i class="fa fa-repeat"></i></a>
                </div>
                <h4 class="panel-title">Daftar Grup</h4>
            </div>
            <div class="panel-body">
              <div class="filterTable">
                <div class="row">
                  <div class="col-4">
                    Aktifitas :
                    <select class="select2" multiple="multiple">
                      <option value="">Semua Aktivitas</option>
                      <option value="">Shalat Tahajud</option>
                      <option value="">Shalat Shubuh Berjamaah</option>
                      <option value="">Tilawah Min 1 Juz</option>
                      <option value="">Infaq</option>
                      <option value="">Menuntut Ilmu</option>
                      <option value="">Membantu Orang Lain</option>
                      <option value="">Silaturrahim</option>
                    </select>
                  </div>
                  <div class="col-4">
                    Grup Aktif :
                    <select class="select2">
                      <option value="">Semua Periode</option>
                      <option value="">7 hari terakhir</option>
                      <option value="">30 hari terakhir</option>
                    </select>
                  </div>
                  <div class="col-4">
                    Grup Konsisten :
                    <select class="select2">
                      <option value="">Semua Periode</option>
                      <option value="">7 hari terakhir</option>
                      <option value="">30 hari terakhir</option>
                    </select>
                  </div>
                </div>
              </div>
              <br>
              <table id="data-table" class="table table-striped table-bordered">
                  <thead>
                      <tr>
                          <th>No</th>
                          <th>Kode Grup</th>
                          <th>Nama Grup</th>
                          <th>Privasi</th>
                          <th>Jumlah Anggota</th>
                          <th>Aksi</th>
                      </tr>
                  </thead>
                  <tbody>

                  </tbody>
              </table>
            </div>
        </div>
    </div>
    </div>

  </div>
</template>
<script>
export default {
  name: 'grouplist',
  data () {
    return {
      page: {
        title: 'Daftar Grup',
        subtitle: 'Informasi mengenai grup-grup yang Anda kelola.'
      }
    }
  },
  mounted () {
    this.handleGrouplist();
    $('.select2').select2();
  },
  methods: {
    handleGrouplist() {
      if ($('#data-table').length !== 0) {
        $('#data-table').DataTable({
            ajax: 'src/assets/data/group.json',
            dom: 'lBfrtip',
            buttons: [
                { extend: 'copy', className: 'btn-sm' },
                { extend: 'csv', className: 'btn-sm' },
                { extend: 'excel', className: 'btn-sm' },
                { extend: 'pdf', className: 'btn-sm' },
                { extend: 'print', className: 'btn-sm' }
            ],
            responsive: true,
            // keys: true,
            // select: true
        });
      }
    }
  }
}
</script>
<style scoped>
  .select2 {
    width: 200px;
  }
</style>



