<template>
  <div id="member-active-chart">
    <div class="panel panel-inverse" data-sortable-id="index-1">
      <div class="panel-heading">
        <div class="panel-heading-btn">
          <a href="javascript:;" class="btn btn-xs btn-icon btn-circle btn-default" data-click="panel-expand"><i class="fa fa-expand"></i></a>
          <a href="javascript:;" class="btn btn-xs btn-icon btn-circle btn-success" data-click="panel-reload"><i class="fa fa-repeat"></i></a>
        </div>
        <h4 class="panel-title">Statistik Anggota (Last 7 Days)</h4>
      </div>
      <div class="panel-body">
        <canvas class="member-chart"></canvas>
      </div>
    </div>
  </div>
</template>
<script>
import Chart from 'chart.js'

export default {
  name: 'member-active-chart',
  data() {

  },
  mounted() {
    this.handleMemberChart();
  },
  methods: {
    randomScalingFactor () {
      return Math.round(Math.random()*100)
    },
    handleMemberChart () {
      var lineChartData = {
          labels: ['January', 'February', 'March', 'April', 'May', 'June', 'July'],
          datasets: [{
              label: 'Anggota Aktif',
              borderColor: '#22ff98',
              pointBackgroundColor: '#22ff98',
              pointRadius: 2,
              borderWidth: 2,
              fill: false,
              data: [this.randomScalingFactor(), this.randomScalingFactor(), this.randomScalingFactor(), this.randomScalingFactor(), this.randomScalingFactor(), this.randomScalingFactor(), this.randomScalingFactor()]
          }, {
              label: 'Anggota Tidak Aktif',
              borderColor: '#ef2d47',
              pointBackgroundColor: '#ef2d47',
              pointRadius: 2,
              borderWidth: 2,
              fill: false,
              data: [this.randomScalingFactor(), this.randomScalingFactor(), this.randomScalingFactor(), this.randomScalingFactor(), this.randomScalingFactor(), this.randomScalingFactor(), this.randomScalingFactor()]
          }]
      };
      var ctx = $('.member-chart');
      var myChart = new Chart(ctx, {
          type: 'bar',
          data: lineChartData,
      });
    }
  }
}
</script>



