import Vue from 'vue'
import Router from 'vue-router'
import Universe from '@/components/page/Universe'
import Analytics from '@/components/page/Analytics'
import Apps from '@/components/page/Apps'
import Home from '@/components/page/Home'
import PushNotif from '@/components/page/PushNotif'
import Login from '@/components/auth/Login'

import GroupList from '@/components/page/group/GroupList'
import Group from '@/components/page/group/Group'

import auth from '@/services/auth.js'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
      meta: { auth: true }
    },
    {
      path: '/group',
      name: 'GroupList',
      component: GroupList,
      meta: { auth: true }
    },
    {
      path: '/group/:code',
      name: 'Group',
      component: Group,
      props: { code: true },
      meta: { auth: true }
    },
    {
      path: '/login',
      name: 'Login',
      components: { unauth: Login }
    },
  ],
  mode: 'history'
})

router.beforeEach((to, from, next) => {
  const auth = JSON.parse(localStorage.getItem('user'))
  if(to.meta.auth == true) {
    if(!auth || !auth.token) {
      next({name: 'Login'})
    }
  }
  next()
})

export default router



