<template>
    <!-- begin login -->
    <div id="login">
      <div class="login login-with-news-feed" v-if="!auth.user.authenticated">
        <!-- begin news-feed -->
        <div class="news-feed">
            <div class="news-image">
                <img src="~@/assets/img/Badr Remarkable.png" data-id="login-cover-image" alt="" />
            </div>
            <div class="news-caption quote-caption">
                <p>{{ quote.content }}</p>
                <span>{{ quote.title }}</span>
            </div>
        </div>
        <!-- end news-feed -->
        <!-- begin right-content -->
        <div class="right-content">
            <!-- begin login-header -->
            <div class="login-header">

            </div>
            <!-- end login-header -->
            <!-- begin login-content -->
            <div class="login-content">
                <div class="alert" :class="alertClass" role="alert">
                  {{ alert }}
                </div>
                <form class="margin-bottom-0" name="loginForm" v-on:submit.prevent="doLogin">
                    <div class="form-group m-b-15">
                        <input type="text" class="form-control input-lg" v-model="form.email" required placeholder="Email" />

                        <div class="invalid-feedback">
                            <div v-if="form.email == ''">Email is required</div>
                        </div>
                    </div>
                    <div class="form-group m-b-15">
                        <input type="password" class="form-control input-lg" v-model="form.password" required placeholder="Password" />

                        <div class="invalid-feedback">
                            <div v-if="form.password == ''">Password is required</div>
                        </div>
                    </div>
                    <div class="login-buttons">
                        <button type="submit" class="btn btn-success btn-block btn-lg">Login</button>
                    </div>
                </form>
                <hr />
                <p class="text-center">
                    &copy; Powered by <a href="http://meetyawme.com">Yawme</a>
                </p>
            </div>
            <!-- end login-content -->
        </div>
        <!-- end right-container -->
      </div>
      <!-- end login -->
    </div>
</template>
<script>
import auth from '@/services/auth.js'

export default {
  name: 'login',
  data () {
    return {
      quote: {
        title: 'The Holy Quran - 94:7',
        content: 'So when you have finished [your duties], then stand up [for worship].'
      },
      error: 0,
      alert: '',
      alertClass: '',
      auth: auth,
      form: {
        email: '',
        password: '',
      },
    }
  },
  methods: {
    doLogin () {
      var data = {
        email: this.form.email,
        password: this.form.password,
      };

      this.$http.post('auth/login', data)
                .then(response => {
                  if (response.data.success === true) {
                    this.reset();
                    this.alertClass = 'alert-success';
                    this.alert = response.data.message;

                    auth.login({
                      access_token: response.data.data.access_token,
                      email: response.data.data.email,
                      name: response.data.data.name,
                    });
                  }
                })
                .catch(error => {
                  this.alertClass = 'alert-danger';
                  this.form.password = '';
                  if (error.response) {
                    this.alert = error.response.data.message;
                  } else {
                    this.alert = 'Terjadi kesalahan saat request data.';
                  }
                });
    },
    reset() {
      this.form.email = '';
      this.form.password = '';
    }
  }
}
</script>
<style lang="scss" scoped>
  @import url('./auth.scss');
  @import url('./login.scss');
</style>



